
import './PortfolioPopup.css';
import React, { useState } from 'react';
import { X, ExternalLink, Eye, Link, Image as ImageIcon } from 'lucide-react';

const PortfolioPopup = ({ isOpen, onClose }) => {
  const [activeTab, setActiveTab] = useState('links');

  const portfolioLinks = [
    {
      title: 'متجر الأزياء الراقية',
      url: 'https://fashion-store.ma',
      description: 'متجر إلكتروني للأزياء النسائية مع نظام دفع متقدم',
      category: 'متجر إلكتروني'
    },
    {
      title: 'عيادة الدكتور أحمد',
      url: 'https://clinic-ahmad.ma',
      description: 'موقع طبي مع نظام حجز المواعيد الذكي',
      category: 'موقع طبي'
    },
    {
      title: 'مكتب المحاماة الذهبي',
      url: 'https://golden-law.ma',
      description: 'موقع قانوني احترافي مع استشارات أونلاين',
      category: 'موقع قانوني'
    },
    {
      title: 'مطعم الأصالة',
      url: 'https://restaurant-asala.ma',
      description: 'موقع مطعم مع نظام طلبات أونلاين',
      category: 'موقع مطعم'
    },
    {
      title: 'شركة البناء المتميزة',
      url: 'https://construction-pro.ma',
      description: 'موقع شركة البناء مع معرض الأعمال',
      category: 'موقع شركة'
    }
  ];

  const portfolioImages = [
    {
      id: 1,
      title: 'متجر الأزياء الراقية',
      image: 'https://images.unsplash.com/photo-1472851294608-062f824d29cc?w=600&h=400&fit=crop',
      category: 'متجر إلكتروني'
    },
    {
      id: 2,
      title: 'عيادة الدكتور أحمد',
      image: 'https://images.unsplash.com/photo-1576091160399-112ba8d25d1f?w=600&h=400&fit=crop',
      category: 'موقع طبي'
    },
    {
      id: 3,
      title: 'مكتب المحاماة الذهبي',
      image: 'https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?w=600&h=400&fit=crop',
      category: 'موقع قانوني'
    },
    {
      id: 4,
      title: 'مطعم الأصالة',
      image: 'https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?w=600&h=400&fit=crop',
      category: 'موقع مطعم'
    },
    {
      id: 5,
      title: 'شركة البناء المتميزة',
      image: 'https://images.unsplash.com/photo-1487058792275-0ad4aaf24ca7?w=600&h=400&fit=crop',
      category: 'موقع شركة'
    },
    {
      id: 6,
      title: 'متجر الإلكترونيات',
      image: 'https://images.unsplash.com/photo-1460925895917-afdab827c52f?w=600&h=400&fit=crop',
      category: 'متجر إلكتروني'
    }
  ];

  if (!isOpen) return null;

  return (
    <div className="popup-overlay">
      <div className="portfolio-popup-container">
        <div className="popup-header">
          <h2>معرض أعمالنا</h2>
          <button className="close-button" onClick={onClose}>
            <X size={24} />
          </button>
        </div>
        
        <div className="portfolio-tabs">
          <button 
            className={`tab-button ${activeTab === 'links' ? 'active' : ''}`}
            onClick={() => setActiveTab('links')}
          >
            <Link size={18} />
            روابط المواقع
          </button>
          <button 
            className={`tab-button ${activeTab === 'images' ? 'active' : ''}`}
            onClick={() => setActiveTab('images')}
          >
            <ImageIcon size={18} />
            صور المشاريع
          </button>
        </div>

        <div className="portfolio-content">
          {activeTab === 'links' && (
            <div className="links-grid">
              {portfolioLinks.map((project, index) => (
                <div key={index} className="link-card">
                  <div className="link-header">
                    <h3>{project.title}</h3>
                    <span className="category-badge">{project.category}</span>
                  </div>
                  <p>{project.description}</p>
                  <div className="link-actions">
                    <a href={project.url} target="_blank" rel="noopener noreferrer" className="visit-link">
                      <ExternalLink size={16} />
                      زيارة الموقع
                    </a>
                    <button className="preview-btn">
                      <Eye size={16} />
                      معاينة
                    </button>
                  </div>
                </div>
              ))}
            </div>
          )}

          {activeTab === 'images' && (
            <div className="images-grid">
              {portfolioImages.map((project) => (
                <div key={project.id} className="image-card">
                  <div className="image-container">
                    <img src={project.image} alt={project.title} />
                    <div className="image-overlay">
                      <h4>{project.title}</h4>
                      <span className="category-badge">{project.category}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default PortfolioPopup;